import translate from "../translations/translations";
import config from "../config";
import {syncMoney, syncUpgrades, syncWorkers} from "../script";
import heatPanel from "./heatPanel";
import {bgmToHigh, bgmToNormal, playSound} from "./playSound";

export default (function() {
  let store;
  let pw;
  let pwcl;
  let pwcNameInput;

  function handleAddProfile() {
    if (pwcNameInput.value.length < 1) {
      return;
    }

    const newPlayer = {name: pwcNameInput.value, money: 0, boughtUpgrades: [], boughtWorkers: [], heat: 0};
    store.profiles.push(newPlayer);
    pwcNameInput.value = "";
    update();
  }

  function handleLoad(i) {
    store.profiles[store.currentProfile].money = store.money;
    store.profiles[store.currentProfile].heat = store.heat;
    store.profiles[store.currentProfile].boughtUpgrades = store.boughtUpgrades;
    store.profiles[store.currentProfile].boughtWorkers = store.boughtWorkers;

    store.currentProfile = i;
    store.money = store.profiles[i].money;
    store.heat = store.profiles[i].heat;
    store.boughtUpgrades = store.profiles[i].boughtUpgrades;
    store.boughtWorkers = store.profiles[i].boughtWorkers;
  }

  function close() {
    playSound("menu_select");
    bgmToNormal();
    pw.classList.add("d-none");
  }

  function open() {
    pw.classList.remove("d-none");
    playSound("menu_select");
    bgmToHigh();
    update();
  }

  function update() {
    try {
      pwcl.removeChild(pwcl.childNodes[0]);
    } catch (err) { }

    const profilesWrapper = document.createElement("div");
    profilesWrapper.className = "save-load-profiles-wrapper";
    for (let i = 0; i < store.profiles.length; i++) {
      const profile = store.profiles[i];

      const profileItem = document.createElement("div");
      profileItem.className = "save-load-profile";

      if (i === store.currentProfile) {
        profileItem.classList.add("save-load-profile--active");
      }

      const profileName = document.createElement("div");
      profileName.className = "save-load-profile-name";
      profileName.innerText = profile.name;

      const profileMoney = document.createElement("div");
      profileMoney.className = "save-load-profile-money";
      profileMoney.innerText = `${translate("money_intro")} ${Math.floor(profile.money)}`;
      if (i === store.currentProfile) {
        profileMoney.innerText = `${translate("money_intro")} ${Math.floor(store.money)}`;
      }

      const loadButton = document.createElement("button");
      loadButton.className = "save-load-profile-load-button";
      loadButton.innerText = translate("load");
      loadButton.addEventListener("click", function () {
        handleLoad(i);
        close();
        syncMoney();
        syncUpgrades();
        syncWorkers();
        heatPanel.syncHeatPanel();
      });

      profileItem.appendChild(profileName);
      profileItem.appendChild(profileMoney);
      profileItem.appendChild(loadButton);
      profilesWrapper.appendChild(profileItem);
    }
    pwcl.appendChild(profilesWrapper);
  }

  return {
    init: function (storeObj) {
      store = storeObj;
      const pwEl = document.createElement("div");
      pwEl.className = "save-load-window d-none";
      pw = pwEl;

      const pwBackground = document.createElement("div");
      pwBackground.className = "save-load-background";
      pwBackground.addEventListener("click", this.close);

      const pwContainer = document.createElement("div");
      pwContainer.className = "save-load-container";

      const pwCloseIcon = document.createElement("div");
      pwCloseIcon.className = "save-load-close-icon";
      pwCloseIcon.innerText = "x";
      pwCloseIcon.addEventListener("click", this.close);

      const pwContainerUpper = document.createElement("div");
      pwContainerUpper.className = "save-load-container-upper";

      const pwContainerCreateUserTitle = document.createElement("div");
      pwContainerCreateUserTitle.className = "save-load-container-create-title";
      pwContainerCreateUserTitle.innerText = translate("create_profile");

      const pwContainerNameInput = document.createElement("input");
      pwContainerNameInput.className = "save-load-container-input";
      pwcNameInput = pwContainerNameInput;

      const pwContainerAddButton = document.createElement("button");
      pwContainerAddButton.className = "save-load-container-add-button";
      pwContainerAddButton.innerText = translate("create_profile_button");
      pwContainerAddButton.addEventListener("click", handleAddProfile);

      const pwContainerList = document.createElement("div");
      pwContainerList.className = "save-load-container-list";
      pwcl = pwContainerList;

      pwContainerUpper.appendChild(pwContainerCreateUserTitle);
      pwContainerUpper.appendChild(pwContainerNameInput);
      pwContainerUpper.appendChild(pwContainerAddButton);

      pwContainer.appendChild(pwCloseIcon);
      pwContainer.appendChild(pwContainerUpper);
      pwContainer.appendChild(pwContainerList);
      pwEl.appendChild(pwBackground);
      pwEl.appendChild(pwContainer);
      document.body.appendChild(pwEl);
    },

    open: open,

    close: close,

    update: update,
  }
})();