import config from "../config";

export default {
  saveProgress: function saveProgress(store) {
    const newSave = {
      currentProfile: store.currentProfile,
      profiles: store.profiles
    }
    newSave.profiles[store.currentProfile] = {
      name: store.profiles[store.currentProfile].name,
      money: store.money,
      boughtUpgrades: store.boughtUpgrades,
      boughtWorkers: store.boughtWorkers,
      heat: store.heat,
    }
    localStorage.setItem(config.saveFileName, JSON.stringify(newSave));
  },

  loadProgress: function loadProgress(store) {
    const save = localStorage.getItem(config.saveFileName);
    if (!save) {
      console.log("No save file found! Startin' a fresh game");
      store.profiles = [
        {
          name: "New Player"
        }
      ]
      return;
    }
    const saveFile = JSON.parse(save);
    const pid = saveFile.currentProfile;
    store.currentProfile = pid;
    store.profiles = saveFile.profiles;
    store.money = saveFile.profiles[pid].money;
    store.boughtUpgrades = saveFile.profiles[pid].boughtUpgrades;
    store.boughtWorkers = saveFile.profiles[pid].boughtWorkers;
    store.heat = saveFile.profiles[pid].heat
  }
}